@import '../../styles/variables';

.main-list{
    padding: 0 0 3rem;
    &__title{
        padding: 0rem 0 2rem;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(14rem,14rem));
        grid-gap: 1rem;
    }
    &__card{
        display: flex;
        position: relative;
        transition: transform 0.4s;
        font-weight: 500;
        z-index: 1; // new
        &:hover{
            transition: transform 0.4s;
            transform:scale(1.05); //old 1.04
            z-index: 0; // new
        }
    }
    &__link{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    &__shadow{
        position: absolute;
        height: 101%;
        width: 100%;
        background: linear-gradient(0deg,#000, transparent);
        border-radius: 4%;
    }
    &__poster{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 4%;
    }
    &__name{
        position: absolute;
        bottom: 1px;
        padding: 1rem;
    }
    &__favourite{
        position: absolute;
        z-index: 2;
        right: 1px;
        padding: 1rem;
    }
    &__favIcon{
        font-size: 2rem;
    }
    &__watchlist{
        position: absolute;
        z-index: 2;
        right: 2.5rem;
        padding: 1rem;
    }
    &__watchIcon{
        font-size: 2rem;
    }
}

.continue-list{
    padding: 0 0 3rem;
    //&__block{}
    //&__seeAll{}
    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__title{
        padding: 0 0 1rem;
    }
    &__subTitle{
        font-size: 1.66rem;
        font-weight: 500;
        padding: 1rem 0 1rem;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(14rem,14rem));
        grid-gap: 1rem;
    }
    &__card{
        display: flex;
        position: relative;
        transition: transform 0.4s;
        font-weight: 500;
        &:hover{
            transition: transform 0.4s;
            transform:scale(1.04);
        }
    }
    &__link{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    &__name{
        word-wrap: break-word;
        font-size: 0.9rem;
    }
    &__info{
        margin-left: 1rem;
        word-wrap: break-word;
    }
    &__tv{
        display: flex;
    }
    &__subtext{
        font-weight: 300;
        font-size: 0.74rem;//0.8
        &:first-child{
            margin-right: 0.4rem;
        }
    }
    &__poster{
        width: 5rem;
        height: 4.3rem;
        object-fit: cover;
        border-radius: 4%;
    }
    &__status{
        position: absolute;
        bottom: 0;
        height: 0.2rem;
        width: 50%;
        border-radius: 2px;
        background: $color-lighter;
    }
    &__gradient{
        position: absolute;
        height: 100%;
        width: 50%;
        border-radius: 2px;
        background: linear-gradient(90deg, rgba(37,37,106,1) 0%, rgba(49,125,140,1) 50%, 
        rgba(128,88,157,1) 75%, rgba(237,12,12,1) 100%);
    }
}

.main-top{
    padding: 0 0 3rem;
    &__title{
        padding: 0rem 0 2rem;
    }
    &__grid{
        display: grid;
        //grid-template-columns: repeat(auto-fit,minmax(35rem,35rem));
        grid-template-columns: repeat(4, 1fr); 
        grid-template-rows: repeat(3, 1fr); 
        grid-gap: 1rem;
    }
    &__card{
        display: flex;
        position: relative;
        transition: transform 0.4s;
        font-weight: 500;
        &:hover{
            transition: transform 0.4s;
            transform:scale(1.02);//1.04
        }
        &:first-child{
            grid-area: 1 / 1 / 4 / 4;
        }
    }
    &__link{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    &__shadow{
        position: absolute;
        height: 100%;
        width: 100%;
        background: linear-gradient(0deg,rgba(0,0,0,0.3), transparent);
        border-radius: 5px;//10
    }
    &__backdrop{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 5px;//10
    }
    &__name{
        position: absolute;
        bottom: 1px;
        font-size: 3rem;
        padding: 1.5rem;
    }
    //
    // mini-version
    //
    &__card-mini{
        display: flex;
        position: relative;
        transition: transform 0.4s;
        font-weight: 500;
        cursor: pointer;
        &:hover{
            transition: transform 0.4s;
            transform:scale(1.04);//1.04
        }
    }
    &__shadow-mini{
        position: absolute;
        height: 100%;
        width: 100%;
        background: linear-gradient(0deg,#000, transparent);
        border-radius: 5px;//10px
    }
    &__name-mini{
        position: absolute;
        bottom: 1px;
        font-size: 1.5rem;
        padding: 0.5rem;
    }
}

.fav-icon{
    &-enter{
        opacity: 0.01;
        &-active{
            opacity: 1;
            transition: opacity 300ms ease-in;
        }
    }
    &-exit{
        opacity: 1;
        &-active{
            opacity: 0.01;
            transition: opacity 300ms ease-in;
        }
        &-done{
            opacity: 0.01;
        }
    }
}