@import '../../styles/variables';

.nav{
    position: relative;
    z-index: 10;
    //
    font-size: 1.3rem;
    font-weight: 600;
    height: 8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
        &__icon{
            padding-left: 0.1rem;
            margin-right: 0.5rem;
        }
        &__list{
            list-style: none;
        }
        &__item{
            align-items: center;
            display: inline-flex;
            cursor: pointer;
            padding-right: 2rem;
        }
        &__link{
            &--active{
                opacity: 0.88;
                &_reverse{
                    filter: opacity(0.55);
                }
            }
        }
        &__auth{
            border-radius: 50%;
            height: 2.5rem;
            width: 2.5rem;
            position: absolute;
        }
        &__dropdown{
            position: absolute;
            margin-top: 2rem;
            margin-left: 3rem;
        }
        &__name{
            padding-left: 3rem;
        }
}
