$background-loading: linear-gradient(30deg, #726e6e, #b4b4b4, #726e6e);
$background-size: 600% 600%;

@mixin anim(){
    background: $background-loading;
    background-size: $background-size;
    -webkit-animation: main-loading 6s ease infinite;
    -moz-animation: main-loading 6s ease infinite;
    animation: main-loading 6s ease infinite;
}

.auth-loading{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
        &__title{
            opacity: 0.1;
        }
}

.continue-loading{
    *{
        border-radius: 1rem;
    }
    .h4{
        @include anim();
        margin: 1rem 0 1rem;
        height: 2.6rem;
        width: 10rem;
    }
    .continue-list__grid{
        div{
            @include anim();
            height: 4rem;
            width: 14rem;
        }
    }
}


.loading-main{
    opacity: 0.6;
}
.text-block-loading{
    border-radius: 3px;
    width: 7rem;
    height: 2rem;
}
.loading-ul li,.loading-dropdown .loading-div-season,.loading-player,.text-block-loading{
    @include anim();
}
.loading-ul li{
    border: none;
    min-width: 10rem;
    min-height: 3rem;
}
.loading-dropdown .loading-div-season{
    border: none;
    border-radius: 3px;
    min-width: 6rem;
    min-height: 2.4rem;
}
.loading-player{
    width: 88.9vw;
    height: 50vw;
}

.loading-anim{
    background: $background-loading;
    background-size: $background-size;
    -webkit-animation: main-loading 6s ease infinite;
    -moz-animation: main-loading 6s ease infinite;
    animation: main-loading 6s ease infinite;
}
@-webkit-keyframes main-loading {
    0%{background-position:0% 81%}
    50%{background-position:100% 20%}
    100%{background-position:0% 81%}
}
@-moz-keyframes main-loading {
    0%{background-position:0% 81%}
    50%{background-position:100% 20%}
    100%{background-position:0% 81%}
}
@keyframes main-loading { 
    0%{background-position:0% 81%}
    50%{background-position:100% 20%}
    100%{background-position:0% 81%}
}