$color-resources-background: #2D2D2D;
$color-loading: #726E6E;
.text-opacity{
    opacity: 0.88;
};


.local-bootstrap{
    @import "~bootstrap/scss/bootstrap";
    $color-service-background: #2D2D2D;
    $color-main:#1d1d1d;
    .list-group-item{
        background-color: #1d1d1d;
    }
    .brdt{
        border: .5px solid rgba(255,255,255,.1);
    }
    .ul-translators li{
        background-color: $color-main;
        border: 1px solid rgba(255,255,255,0.15);
    }
    .ul-translators li:hover{
        opacity: 0.88;
    }
    .ul-translators span:hover{
        cursor: pointer;
    }
    .trs-active,.sne-active button{
        opacity: 0.7;
    }
    .dropdown-menu .sne-active {
        background: #2D2D2D70;
    }
    .series-content .dropdown-menu{
        background: $color-main;
    }
    .series-content .dropdown-item:hover{
        background-color: #2D2D2D70;
        opacity: 0.88;
    }
    .series-content .dropdown button,.btn-primary:hover{
        background: $color-main;
        border: 1px solid rgba(255,255,255,0.15);
    }
    .series-content .show .btn-primary.dropdown-toggle{
        background: $color-main;
        border: 1px solid rgba(255,255,255,0.15);
    }
    .series-content .dropdown .btn-primary:hover{
        opacity: 0.88;
    }
}

.backdrop{
    position: absolute;
    width: 100%;
    height: 90vh;
}
.backdrop img{
    width: 100%;
    height: 100%;
    background: $color-loading;
    object-fit: cover;
}

.popup-info{
    position: relative;
    width: 100%;
    height: 90vh;
}
.popup-info .back-span{
    position: absolute;
    cursor: pointer;
    font-size: 2.6em;
    text-shadow: 0 .5rem 1rem rgba(0,0,0,.40);
    left: 0;
    z-index: 1;
}
.popup-info .description{
    overflow-wrap: break-word;
    text-align: justify;
    width: 80%;
}
.popup-info .blur{
    position: absolute;
    bottom: 0;
    -webkit-backdrop-filter: blur(10px);
    width: 100%;
    height: 100%;
    -webkit-mask-image: linear-gradient(to bottom,transparent 50%,#000 75%);
    background: linear-gradient(to bottom,transparent 50%,rgba(0,0,0,.25) 60%,rgba(0,0,0,.7) 100%);
}
.backdrop-content{
    position: absolute;
    bottom: 0;
}

.resources{
    background-color: $color-resources-background;
}
.resources .flex{
    display: flex;
}
.resources .service{
    border-radius: 25px;
    background: linear-gradient(30deg, rgba(33,33,33,1) 0%, rgba(27,27,27,1) 100%);
    height: 6rem;
    width: 10rem;
    border: none;
}
.resources .service:hover{
    opacity: 0.88;
}
.resources .service-p{
    font-size: 1.3rem;
}
.resources .hdrezka-service{
    background: linear-gradient(30deg, rgba(214,49,78,1) 0%, rgba(229,104,46,1) 100%);
}
.resources .gidonline-service{
    background: linear-gradient(30deg, rgba(57,106,37,1) 0%, rgba(79,171,67,1) 100%);
}

.playerjs .player{
    min-width: 88.9vw;
    min-height: 50vw;
}

.fade-in{
    animation: fadeIn ease 7s;
    -webkit-animation: fadeIn ease 7s;
    -moz-animation: fadeIn ease 7s;
    -o-animation: fadeIn ease 7s;
    -ms-animation: fadeIn ease 7s;
}
@keyframes fadeIn {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}
@-moz-keyframes fadeIn {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}
@-webkit-keyframes fadeIn {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}